<template>
  <div id="app">
    <img class="vue-logo" src="./assets/logo.png" />
    <nav class="navbar" role="navigation" aria-label="main navigation">
      <div class="navbar-brand">
        Vue Index
      </div>
    </nav>
    <div class="page">
      <Index/>
    </div>
    <footer class="footer">
      <div class="container">
        <div class="content has-text-centered">
          Made with &lt;3 by <a href="https://gwenaelp.github.io" target="_blank">Gwenaelp</a>
        </div>
      </div>
    </footer>
  </div>
</template>
<script>
import Vue from 'vue';
import 'mdi/css/materialdesignicons.min.css';
import Index from '@/components/Index';

export default {
  name: 'app',
  components: {
    Index,
  },
};
</script>

<style lang="scss">
@import url('https://fonts.googleapis.com/css?family=Dosis');

html,
body {
  margin:0;
  padding:0;
  height:100%;
  background: #35495E;
}

.page {
  padding-top: 14px;
  background: white;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.navbar {
  background: #41B883;
  font-family: 'Dosis', sans-serif;
  font-size: 26pt;
}

.navbar-brand {
  margin-left: 140px;
}

.footer {
  background: #35495E;
  color: white;
}

.input:active, .input:focus {
  border-color: #41B883 !important;
}

.button.is-primary {
  background-color: #41B883 !important;
  transition: background-color 0.3s !important;
}
.button.is-primary:hover {
  background-color: #35495E !important;
}

.footer {
}

.footer a {
  font-weight: bold;
  color: white;
}

.footer a:hover {
  text-decoration: underline;
}

::selection {
  background: #35495E; /* WebKit/Blink Browsers */
  color:white;
}
::-moz-selection {
  background: #35495E; /* Gecko Browsers */
  color:white;
}

.vue-logo {
  position: absolute;
  z-index: 10;
  pointer-events: none;
}
</style>



