<template>
  <div>
      <b-field class="searchinputField">
        <b-input icon="magnify" class="searchInput" type="search" placeholder="Search..." v-model="searchValue"></b-input>
      </b-field>
    </b-field>
    <div style="clear: both;"></div>
    <div class="tagsList">
      <b-tag v-bind:class="getTagClass()">
        <span v-on:click="filterByTag(undefined)">
          All
        </span>
      </b-tag>
      <b-tag v-bind:class="getTagClass(tag)" v-for="tag in tagsLimited">
        <span v-on:click="filterByTag(tag.name)">
          {{tag.name}} ({{tag.count}})
        </span>
      </b-tag>
    </div>
    <b-table
        :data="entries"
        >
        <template slot-scope="props">
            <b-table-column field="name" label="Name" width="150">
              <div style="font-weight: bold">
                {{ props.row.name }}
              </div>
              <a :href="props.row.github"><i class="mdi mdi-github-circle"></i></a>
              <a :href="props.row.homepage"><i class="mdi mdi-home"></i></a>
            </b-table-column>

            <b-table-column field="description" label="Description">
              {{ props.row.description }}
              <div>
                <b-tag v-for="topic in props.row.topics" style="margin-left: 2px; margin-right: 2px; padding-left: 0.75em; padding-right: 0.75em;">
                  {{topic}}
                </b-tag>
              </div>
            </b-table-column>

            <b-table-column field="license" label="License">
               {{ props.row.license }}
            </b-table-column>

            <b-table-column field="owner" label="Owner">
              <img :src="props.row.owner.avatar" :alt="name" class="avatar" />
              <span style="font-size: 80%">
                {{ props.row.owner.name }}
              </span>
            </b-table-column>

            <b-table-column field="stargazers" label="Popularity">
              <i class="mdi mdi-eye"></i> {{ props.row.popularity.watch }} <br/>
              <i class="mdi mdi-star"></i> {{ props.row.popularity.stars }} <br/>
              <i class="mdi mdi-source-fork"></i> {{ props.row.popularity.forks }} <br/>
            </b-table-column>

            <b-table-column field="last_update" label="Last activity" width="120">
              <timeago :since="props.row.activity.last_push" class="timeago"></timeago>
            </b-table-column>
        </template>
        <template slot="empty">
          <section class="section">
            <div class="content has-text-grey has-text-centered">
              <p>
                <b-icon
                  icon="emoticon-sad"
                  size="is-large">
                </b-icon>
              </p>
              <p>Nothing found.</p>
            </div>
          </section>
        </template>
    </b-table>
    <div v-if="moreData" class="moreDataBtnContainer">
        <span style="width: 100%; text-align: center;" v-observe-visibility="showMoreEntries">
          More entries ...
        </span>
    </div>
    <div style="clear: both"></div>
  </div>
</template>

<script>
function debounce(func, wait, immediate) {
  var timeout;
  return function() {
    let context = this, args = arguments;
    const later = function() {
      timeout = null;
      if (!immediate) func.apply(context, args);
    };
    let callNow = immediate && !timeout;
    clearTimeout(timeout);
    timeout = setTimeout(later, wait);
    if (callNow) func.apply(context, args);
  };
}

function getCORS(url, success) {
  let xhr = new XMLHttpRequest();
  if (!('withCredentials' in xhr)) xhr = new XDomainRequest(); // fix IE8/9
  xhr.open('GET', url);
  xhr.onload = success;
  xhr.send();
  return xhr;
}
/** Plugin proposal code
    <button class="button is-primary newEntryBtn" slot="trigger" @click="isNewEntryModalActive = true">
      <span><i class="mdi mdi-plus-circle"></i> Propose a new entry</span>
    </button>
    <b-modal :active.sync="isNewEntryModalActive">
      <div class="card">
        <header class="card-header">
          Propose a new entry
        </header>
        <div class="card-body">
          <p>Paste here the Github adress of the repository you want to add.</p>
          <b-input icon="github-circle" name="subject" v-model="repoProposal" expanded></b-input>
          <p>Please note that the review process is manual and might take some time.</p>
          <Recaptcha
            @verify="onVerify"
            @expired="onExpired"/>
          <b-field horizontal>
            <p class="control">
              <button class="button is-primary" @click="sendProposal">
                Ok
              </button>
            </p>
          </b-field>
        </div>
      </div>
    </b-modal>
*/
export default {
  name: 'Index',

  data() {
    this.loadAsyncData();

    this.debouncedLoadAsyncData = debounce(function() {
      this.loadAsyncData();
    }, 250);

    var _this = this;

    getCORS(`http://ks.utopland.net:3000/tags`, (request) => {
      let response = request.currentTarget.response || request.target.responseText;
      for (var i = 0; i < response.length; i++) {
        response[i];
      }

      response = JSON.parse(response);

      _this.tagsLimited = response.splice(0, 10);
      _this.tags = response;
    });

    return {
      isNewEntryModalActive: false,
      searchValue: '',
      tagSelectedValue: undefined,
      page: 1,
      skip: 0,
      entries: [],
      tags: [],
      repoProposal: '',
      tagsLimited: [],
      moreData: true,
    };
  },

  methods: {
    showMoreEntries() {
      this.loadAsyncData(true);
    },

    sendProposal() {
      alert(this.repoProposal);
      let xhr = new XMLHttpRequest();
      if (!('withCredentials' in xhr)) xhr = new XDomainRequest(); // fix IE8/9
      xhr.open('POST', 'http://ks.utopland.net:2403/proposals');
      xhr.setRequestHeader('Content-Type', 'application/json');
      //xhr.onload = success;
      xhr.send('{"repo": "'+ this.repoProposal + '"}');
      return xhr;

    },

    getTagClass(tag) {
      if(!tag && this.tagSelectedValue === undefined)
        return 'tag-clickable active';
      else if(tag && tag.name === this.tagSelectedValue)
        return 'tag-clickable active';
      else
        return 'tag-clickable';
    },

    onExpired() {
      alert('onExpired');
    },

    onVerify() {
      alert('onVerify');
    },

    filterByTag(tag) {
      this.tagSelectedValue = tag;
    },

    loadAsyncData(addToData) {
      const page = this.page || 1;
      const limit = 20;

      if(addToData)
          this.skip += limit;
      else {
        this.moreData = true;
        this.skip = 0;
      }
      const skip = this.skip;



      let search = '';
      if (this.searchValue !== '' && this.searchValue !== undefined) {
        search = `search=${this.searchValue}&`;
      }

      let tagFilterChunk = '';
      if(this.tagSelectedValue) {
        tagFilterChunk = `topic=${this.tagSelectedValue}&`;
      }

      getCORS(`http://ks.utopland.net:3000/entries?${tagFilterChunk}${search}limit=${limit}&skip=${skip}`, (request) => {
        let response = request.currentTarget.response || request.target.responseText;
        if(response.length < limit)
          this.moreData = false;

        for (var i = 0; i < response.length; i++) {
          response[i];
        }
        if(addToData) {
          var newEntries = [];

          for (var i = 0; i < this.entries.length; i++) {
            newEntries.push(this.entries[i]);
          }
          response = JSON.parse(response);

          for (var i = 0; i < response.length; i++) {
            newEntries.push(response[i]);
          }

          this.entries = newEntries;
        }
        else {
          this.skip = 0;
          this.entries = JSON.parse(response);
        }
      });
    },

    onPageChange(page) {
      this.page = page;
      this.loadAsyncData();
    },
  },

  //components: { Recaptcha },

  watch: {
    searchValue() {
      this.debouncedLoadAsyncData();
    },

    tagSelectedValue() {
      this.loadAsyncData();
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

.timeago {
  font-size: 80%;
}

.newEntryBtn {
  float: right;
  margin-right:40px;
  margin-bottom: 20px;
}

.avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.searchinputField {
  float: left;
  margin-left:120px;
}

.card-header {
  padding: 20px;
  font-weight: bold;
}

.card-body{
  padding: 20px;
}

.card-body .control {
  margin-top: 14px;
  margin-bottom: 14px;
}
.card-body button {
  float: right;
}

.tag-clickable {
  margin-left: 2px;
  margin-right: 2px;
  padding-left: 0;
  padding-right: 0;
  z-index: 100;
}

.tag-clickable:hover, .tag-clickable.active {
  background: #363636;
  color: #F5F5F5;
  cursor: pointer;
}

.tag-clickable span {
  padding-left: 0.75em;
  padding-right: 0.75em;
}
.tagsList {
  margin-top: 10px;
  margin-left: 10px;
}

.moreDataBtnContainer {
  padding: 20px;
}
</style>



